@import url('https://fonts.googleapis.com/css?family=Raleway'); /* Raleway Font Import */
@import url('https://fonts.googleapis.com/css?family=Roboto'); /* Roboto Font Import */

.About {
  position: relative;
  height: 100vh;
  width: 100%;
  font-family: "Raleway", sans-serif;
  overflow-y: hidden;
  background-color: #0d0d0d;
  -webkit-transition: 2s;
  -o-transition: 2s;
  transition: 2s;
}

.About-header {
  position: absolute;
  top: -5%;
  left: -12%;
  font-family: "Raleway", sans-serif;
  font-weight: 400;
  font-size: 80px;
  color: #dedede;
  -webkit-animation: 2s linear slideInLeft;
          animation: 2s linear slideInLeft;
}

.About-contents-box {
  position: absolute;
  height: 45vh;
  width: 60%;
  top: 25%;
  left: 20%;
  border-style: solid;
  border-color: #dedede;
  border-width: medium;
  -webkit-animation: 1.5s linear slideDown;
          animation: 1.5s linear slideDown;
}

.About-text {
  position: absolute;
  top: 20%;
  left: 20%;
  font-family: "Roboto", sans-serif;
  font-size: 30px;
  color: #dedede;
}

.About-github-icon {
  position: absolute;
  height: 6vh;
  width: 5%;
  top: 70%;
  left: 5%;
  background-image: url(../images/icons/GitHub.png);
  background-repeat: no-repeat;
  background-size: 100%;
}

.About-facebook-icon {
  position: absolute;
  height: 6vh;
  width: 5%;
  top: 70%;
  left: 20%;
  background-image: url(../images/icons/Facebook.png);
  background-repeat: no-repeat;
  background-size: 100%;
}

.About-linkedin-icon {
  position: absolute;
  height: 7vh;
  width: 5.8%;
  top: 69%;
  left: 35%;
  background-image: url(../images/icons/Linkedin2.png);
  background-repeat: no-repeat;
  background-size: 100%;
}

.About-Portfolio {
  position: absolute;
  top: -55%;
  left: -10%;
  font-family: "Raleway", sans-serif;
  font-size: 20px;
  font-weight: 800;
  color: #dedede;
  -webkit-transition: 2s;
  -o-transition: 2s;
  transition: 2s;
}

.About-Portfolio:hover {
  cursor: pointer;
  letter-spacing: 30px;
}

.About-Contact {
  position: absolute;
  top: -55%;
  left: 100%;
  font-family: "Raleway", sans-serif;
  font-size: 20px;
  font-weight: 800;
  color: #dedede;
  -webkit-transition: 2s;
  -o-transition: 2s;
  transition: 2s;
}

.About-Contact:hover {
  cursor: pointer;
  letter-spacing: 30px;
}

/*-------- END --------*/

/*======== Animations ========*/

@-webkit-keyframes scrollUp {
  0%,
  50% {
    height: 1000px
  }

  100% {
    height: 0px;
  }
}

@keyframes scrollUp {
  0%,
  50% {
    height: 1000px
  }

  100% {
    height: 0px;
  }
}

@-webkit-keyframes slideDown {
  0%,
  50% {
    top: -60%;
  }

  100% {
    top: 25%;
  }
}

@keyframes slideDown {
  0%,
  50% {
    top: -60%;
  }

  100% {
    top: 25%;
  }
}

@-webkit-keyframes slideInLeft {
  0%,
  50% {
    left: -100%;
  }

  100% {
    left: -12%
  }
}

@keyframes slideInLeft {
  0%,
  50% {
    left: -100%;
  }

  100% {
    left: -12%
  }
}

@-webkit-keyframes slideInRight {
  0%,
  50% {
    left: 200%;
  }

  100% {
    left: 20%;
  }
}

@keyframes slideInRight {
  0%,
  50% {
    left: 200%;
  }

  100% {
    left: 20%;
  }
}

/*-------- END --------*/



